/*кнопка*/
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  display: block;
  width: 50px;
  height: 50px;
  background-color: #0ad9c6;
  cursor: pointer;
  border: none; }
  @media (min-width: 1024px) {
    .main-nav__toggle {
      display: none; } }
  .main-nav__toggle:hover {
    opacity: 0.7;
    cursor: pointer; }

.main-nav__toggle span {
  font-size: 0; }

/*закрытое состояние*/
.main-nav--closed .main-nav__toggle span {
  display: block;
  width: 22.5px;
  height: 1px;
  background-color: #000000;
  border-radius: 1px;
  transform: translateX(7px); }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  right: 14px;
  width: 22.5px;
  height: 1px;
  background-color: #000000;
  border-radius: 1px; }

.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 14px;
  width: 22.5px;
  height: 1px;
  background-color: #000000;
  border-radius: 1px; }

/*открытое состояние*/
.main-nav--opened .main-nav__toggle {
  background-color: #ffffff;
  z-index: 3; }

.main-nav--opened .main-nav__toggle span::before, .main-nav--opened .main-nav__toggle span::after {
  content: "";
  position: absolute;
  top: 21px;
  right: 11px;
  width: 29px;
  height: 1px;
  border: 1px solid rgba(20, 21, 23, 0.2); }

.main-nav--opened .main-nav__toggle span::before {
  transform: rotate(45deg); }

.main-nav--opened .main-nav__toggle span::after {
  transform: rotate(-45deg); }

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__item {
    position: relative;
    background-color: #f6f6f6;
    height: 60px;
    padding: 15px 0 0 16px;
    margin-bottom: 30px; }
    .main-nav--opened .main-nav__item:last-child {
      margin-bottom: 0; }
    .main-nav--opened .main-nav__item::after {
      position: absolute;
      content: "";
      background-image: url("../img/arrow.svg");
      height: 12px;
      width: 7.4px;
      top: 23px;
      right: 25px;
      background-repeat: no-repeat; } }

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__list {
    padding-top: 100px;
